
















































	var utils = require('../../common/utils').utils;
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		data() {
			return {
				title: 'textarea',
				focus: false,
				num: 0,
				imageList: [],
				faultItems: [],
				reply_imgs: '',
				to_user: '',
				rec_id: '',
				tempFilePaths: '',
				text: '',
				room_id: ''
			}
		},
		computed: mapState(['faults', 'fault_dev', 'lables']),
		onLoad(options) {
			var _this = this
			console.log(options);
			_this.rec_id  = options.rec_id;
			_this.to_user = options.to_user;
			_this.room_id = options.room_id;
		},
		methods: {
			...mapMutations(['setFaults']),
			getLeng(e){
				this.num = e.detail.value.length
				this.text = e.detail.value
			},
			// 删除图片
			getRecord(event){
				var indx = event.target.dataset.idx;
				this.imageList.splice(indx,1)
			},
			chooseImage: async function () {
				if(this.imageList.length === 4){
					let isContinue = await this.isFullImg();
					console.log("是否继续?",isContinue);
					if(!isContinue){
						return;
					}
				}
				uni.chooseImage({  
					count: 4,
					sizeType: ['original'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album'], //从相册选择
					success: (res) => {
						console.log(JSON.stringify(res.tempFilePaths));
						this.tempFilePaths = res.tempFilePaths[0]
						this.imageList = this.imageList.concat(res.tempFilePaths);
						console.log(this.imageList)
					}
				});
			},
			isFullImg:function(){
				return new Promise((res) => {
					uni.showModal({
						content:"已经有4张图片了,是否清空现有图片？",
						success: (e) => {
							if(e.confirm){
								this.imageList = [];
								res(true);
							}else{
								res(false)
							}
						},
						fail:() => {
							res(false)
						}
					})
				})
			},
			// 获取设备
			geteEuipment() {
				var that = this
				uni.navigateTo({
					url: '../equipment/equipment?room_id=' +that.room_id
				})
			},
			getSubmit() {
				// 判断有没有输入故障描述
				if (this.num === 0) {
					uni.showToast({
						title: '请输入故障描述',
						duration: 2000,
						icon: 'none'
					});
				}else {
					var _this = this;
					// 判断有没有选择图片
					if (_this.imageList.length === 0){
						// 判断有没有选择故障设备
						if(_this.faults.length === 0){
							uni.showToast({
								title: '至少选择一件故障设备',
								duration: 2000,
								icon: 'none'
							});
						}else if(_this.faults.length > 0) {
							console.log(_this.text)
// 							uni.navigateBack({
// 								delta: 1
// 							});
							var _data = {
								action: 'fault',
								reply_imgs: _this.reply_imgs,
								reply_desc: '故障原因/修复过程:' + _this.text +'<br />故障设备:'+String(_this.lables).replace(',', '、'),
								to_user: _this.to_user,
								rec_id: _this.rec_id,
								fault_dev: _this.fault_dev
							}
							utils.requestPost('operation/postreply', _data, function(res){
								if(res.code === 0) {
									uni.navigateBack({
										delta: 1
									});
								}
							})
						}
					}else {
						if(_this.faults.length === 0) {
							uni.showToast({
								title: '至少选择一件故障设备',
								duration: 2000,
								icon: 'none'
							});
						}else if (_this.faults.length > 0) {
							uni.showToast({
								title: '正在上传图片',
								duration: 800,
								icon: 'none'
							})
							uni.uploadFile({
								url: 'https://' + utils.host + '/weixinweb/xiaochengcxufile/1',
								filePath: _this.tempFilePaths,
								name: 'upfile',
								success: (res) => {
									var data = JSON.parse(res.data);
									_this.reply_imgs = data.url;
									var _data = {
										action: 'fault',
										status: 9,
										reply_imgs: _this.reply_imgs,
										reply_desc: '故障原因/修复过程:' + _this.text +'<br />故障设备:'+_this.lables[0],
										to_user: _this.to_user,
										rec_id: _this.rec_id,
										fault_dev: _this.fault_dev
									}
									utils.requestPost('operation/postreply', _data, function(res){
										if(res.code === 0) {
											uni.navigateBack({
												delta: 1
											});
										}
									})
								}
							})
						}
					}
				}
				this.setFaults('')
			}
		}
	}



